import Swal from "sweetalert2";
import { AxiosMasterUpdate } from "./Axios";

export const warning = (pesan) => {
  return new Promise((resolve, reject) => {
    Swal.fire({
      position: "top-right",
      icon: "warning",
      text: pesan,
      timer: 2500,
      showConfirmButton: false,
    })
      .then(() => resolve("Berhasil"))
      .catch(() => reject("Gagal"));
  });
};

export const berhasil = (pesan) => {
  return new Promise((resolve, reject) => {
    Swal.fire({
      position: "top-right",
      icon: "success",
      text: pesan,
      timer: 2500,
      showConfirmButton: false,
    })
      .then(() => resolve("Berhasil"))
      .catch(() => reject("Gagal"));
  });
};

export const gagal = (pesan) => {
  return new Promise((resolve, reject) => {
    Swal.fire({
      position: "top-right",
      icon: "error",
      text: pesan,
      timer: 2500,
      showConfirmButton: false,
    })
      .then(() => resolve("Berhasil"))
      .catch((err) => reject(err));
  });
};

export const reactive = (err, kode, endpoint, data) => {
  return new Promise((resolve, reject) => {
    Swal.fire({
      title: "Oopps..",
      text: err.response.data,
      icon: "info",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Re-Activate",
      cancelButtonText: "Cancel",
    }).then((result) => {
      if (result.isConfirmed) {
        AxiosMasterUpdate(endpoint + kode, data)
          .then(() => berhasil("Data Berhasil Dikembalikan"))
          .then(() => resolve("Berhasil"))
          .catch((err) => reject("Gagal"));
      }
    });
  });
};

export const LogData = (keterangan, data, err) => {
  let tanggal = new Date().toLocaleDateString();
  let log = JSON.parse(localStorage.getItem("LogWebsite")) || [];
  let datanya = {
    tanggal: tanggal,
    keterangan: keterangan,
    data: data,
    error: err,
  };
  log.push(datanya);
  localStorage.setItem("LogWebsite", JSON.stringify(log));
};
