import React, { Component } from "react";

class HeaderCard extends Component {
  render() {
    return (
      <h4
        className="card-header mb-3"
        style={{
          fontSize: 25,
          backgroundColor: "#394867",
          borderTopLeftRadius: 10,
          borderTopRightRadius: 10,
          height: 50,
          color: "#ffffff",
        }}
      >
        {this.props.title}
      </h4>
    );
  }
}

export default HeaderCard;
